const IntvVariantPrompts = ({intvID, variantJSON, intvLabel, variantDesc, contexts, intvGetter, promptValueGetter, promptValueSetter}) => {
    // const {scenario, scenarioType} = useScenarioEditor()
    const [intv, setIntv] = useState(null)

    useEffect(
        () => {
            if (!intv) {
                setIntv(intvGetter(intvID))
            }
        }, [intvID, intv, intvGetter]
    )
    if (!intv) {
        return (<div>calculating</div>)
    }
    return (
        <div>
            <div>
                <b>Intervention: </b> {intvLabel}
                {"" === variantDesc ? " " : `: ${variantDesc} `}
                <b> Used By: </b> {contexts}
            </div>
            <ul>
            {intv.prompts.map(prompt =>
                <li key={prompt.id}>
                    <IntvVariantPrompt
                        intv={intv}
                        variant={variantJSON}
                        prompt={prompt}
                        stateGetter={promptValueGetter}
                        stateSetter={promptValueSetter}
                    />
                </li>
            )}
            </ul>

        </div>
    )

}


export default IntvVariantPrompts