export const loadIntvs = () => {
    const tmp = INTVS.map(e => {
        const rec = {...e}
        if (!e.abbrLabel) {
            rec.abbrLabel = e.label
        }
        if (!e.distPrompts) {
            rec.distPrompts = []
        }
        if (!e.prompts) {
            rec.prompts = []
        }
        return rec
    })
    return tmp
}

export const loadIntvPrompts = () => {
    const tmp = INTV_PROMPTS.map(e => {
        const rec = {...e}
        if (!!e.interventionID) {
            const found = INTVS.find(i => i.id === e.interventionID)
            if (!found) {
                console.log(`loadIntvPrompts(${e.id}) ${e.interventionID} not found`)
            }
        }
        if (!e.abbrLabel) {
            rec.abbrLabel = ""
        }
        if (!e.feedback) {
            rec.feedback = rec.label
        }
        if (!e.answers) {
            rec.answers = []
        }
        return rec
    })
    return tmp
}

export const loadIntvPromptAnswers = () => {
    const tmp = INTV_PROMPT_ANSWERS.map(e => {
        if (!e.abbrLabel) {
            e.abbrLabel = ""
        }
        const found = INTV_PROMPTS.find(p => p.id === e.promptID)
        if (!found) {
            console.log(`loadIntvPromptAnswers(${e.id}) ${e.promptID} not found`)
        }
        return e
    })
    return tmp
}

export const getScenarios = (db, scenarioType, schemaVersion) => {
    const query = db.collection("scenarios")
             .where("scenarioType", "==", scenarioType)
             .where("schemaVersion", "==", schemaVersion)
    return query.get()
        .then(snapShot => {
            const scenarios = []
            if (snapShot.size > 0) {
                snapShot.docs.forEach(doc => {
                    const data = doc.data()
                    scenarios.push({
                        id: data.id,
                        name: data.name,
                        scenarioType: data.scenarioType,
                        schemaVersion: data.schemaVersion
                    })
                })
            }
            return scenarios
        })
}

