export const getScenarioTypeByID = (db, scenarioTypeID) => {
    return db.collection("scenario-types")
        .doc(scenarioTypeID)
        .get()
        .then(doc => {
        const data = doc.data()
        data.id = doc.id
        return addMissingScenarioTypeFields(data)
        })
        .catch(error => {
            console.log(error)
            return error
        })
}

export const getScenarioType = (db, scenarioType, schemaVersion) => {
    return db.collection("scenario-types")
            .where("name", "==", scenarioType)
            .where("schemaVersion", "==", schemaVersion)
            .limit(1)
            .get()
            .then(snapShot => {
                if (snapShot.size === 1) {
                    return addMissingScenarioTypeFields(snapShot.docs[0].data())
                } else {
                    throw new Error("document not found")
                }
            })
}



// FIXME: this needs to go away, although I need to figure out if it's in use anywhere first
export const getPromptAndOptions = (scenarioTypeOrPubScenario, promptID) => {
    const scenType = scenarioTypeOrPubScenario
    const prompt = scenType.interventionPrompts.find(e => e.id === promptID)
    if (!!prompt) {
        const options = prompt.answers.map(ansID => scenType.interventionPromptAnswers.find(a => a.id === ansID))
        prompt.options = options.map(opt => {
            return {...opt, prompt: prompt.label}
        })
    }
    return prompt
}

