export const createInterventionVariant = (distPromptIds2OptIds) => {
    // the list of opt IDs mapped to each distPromptID needs to be sorted in order
    // to make them comparable
    const retVal = {}
    Object.entries(distPromptIds2OptIds).forEach(([distPromptID, optionsArray]) => {
        retVal[distPromptID] = optionsArray.sort()
    })
    return retVal
}

export const interventionVariantsAreEqual = (intvVariant1, intvVariant2) => {
    return _.isEqual(intvVariant1, intvVariant2)
}


export const intvVariant2JSON = (intvVariant) => {
    return jsonStableStringify(intvVariant)
}